"use client";

import { useGetAccountQuery } from "@/redux/api/profile";

interface IAccount {
  user: string;
  avatar: string | null;
  about: string | null;
}

const Profile = () => {
  const { data, isLoading, error } = useGetAccountQuery<IAccount>();

  console.log(data);

  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error loading data</div>;
  }

  return (
    <div>
      <h1>{user?.user}</h1>
      {user?.avatar && <img src={user.avatar} alt="User Avatar" />}
      <p>{user?.about}</p>
    </div>
  );
};

export default Profile;
